.rs-modal {
    width: 90%;
    max-width: 1200px;
    /* max-height: 90vh; */
    position: absolute;

    background-color: #fff;

    top: 70px;
    left: 50%;
    z-index: 1000;
    transform: translateX(-50%);

    box-shadow: 0px 4px 12px 0px #5a5a5a6b;

    padding: 20px;
    margin-bottom: 50px;
}

.rs-modal-open {
    display: block;
}

.rs-modal-close {
    display: none;
}

.rs-modal-head {
    display: flex;
    justify-content: space-between;
}
