html .rs-delete td:not(:last-child){
    background-color: #ffcccb;
    text-decoration: line-through;
}

html .rs-add td:not(:last-child){
    background-color: #e7ffe3;
}

html .rs-scip,
html .rs-scip:active,
html .rs-scip:focus {
    background-color: yellow;
}

html .rs-visit,
html .rs-visit:active,
html .rs-visit:focus {
    background-color: #54ff54;
}
