.fc-button-primary {
    color: #9d9d9d;
    border-color: #fe17bf;
    background-color: #ffffff;
}
.fc-button-primary:not(:disabled):active, 
.fc-button-primary:not(:disabled).fc-button-active {
  background-color: #fe17bf;
  border-color: #fe17bf;
  color: #fff;
}
.fc-button-primary:hover {
  background-color: transparent;
  color: #fe17bf;
  border-color: #fe17bf;
}

.fc-button-primary:not(:disabled):active:focus,
.fc-button-primary:not(:disabled).fc-button-active:focus {
    box-shadow: none;
}
.fc-button:focus,
.fc-button-primary:focus {
  box-shadow: none;
}
.f-left {
  float: left;
}

.calendar-app a.fc-event,
.calendar-app a.fc-event:hover {
  color: #fff;
}

.calendar-app .fc-day-header a {
  color: #fe17bf;
}

.calendar-app .fc-day-header a:hover {
  color: #e600a8;
}